<template>
    <div class="settings">
      <h2>个人设置</h2>
      <button class="back-button" @click="goBack">
        <img src="@/assets/back-icon.png" alt="返回" class="button-icon">
        <span>返回</span>
      </button>
      <div class="settings-section">
        <h3>个人信息</h3>
        <form @submit.prevent="updatePersonalInfo">
          <div class="form-group">
            <label for="nickname">昵称</label>
            <input type="text" id="nickname" v-model="personalInfo.nickname" />
          </div>
          <div class="form-group">
            <label for="gender">性别</label>
            <select id="gender" v-model="personalInfo.gender">
              <option value="male">男</option>
              <option value="female">女</option>
              <option value="other">其他</option>
            </select>
          </div>
          <div class="form-group">
            <label for="hobbies">爱好</label>
            <input type="text" id="hobbies" v-model="personalInfo.hobbies" placeholder="用逗号分隔多个爱好" />
          </div>
          <button type="submit">保存个人信息</button>
        </form>
      </div>
      <div class="settings-section">
        <h3>账号安全</h3>
        <form @submit.prevent="updateAccountSecurity">
          <div class="form-group">
            <label for="password">新密码</label>
            <input type="password" id="password" v-model="accountSecurity.password" />
          </div>
          <div class="form-group">
            <label for="phone">手机号</label>
            <input type="text" id="phone" v-model="accountSecurity.phone" />
          </div>
          <button type="submit">保存账号安全设置</button>
        </form>
      </div>
    </div>
  </template>
  
  <script>
  import { updateUser } from '@/api/user.js';

  export default {
    name: 'Settings',
    data() {
      return {
        personalInfo: {
          nickname: '',
          gender: '',
          hobbies: ''
        },
        accountSecurity: {
          password: '',
          phone: ''
        }
      }
    },
    methods: {
     async updatePersonalInfo() {
      const userId = localStorage.getItem('currentUserId'); // 假设用户ID存储在localStorage中
      const userData = {
        ...this.personalInfo,
        ...this.accountSecurity
      };
      try {
        const updatedUser = await updateUser(userId, userData);
        console.log('更新个人信息:', updatedUser);
        alert('个人信息已更新');
      } catch (error) {
        console.error('Error updating personal info:', error);
        alert('更新个人信息失败，请重试');
      }
    },
    async updateAccountSecurity() {
      const userId = localStorage.getItem('currentUserId'); // 假设用户ID存储在localStorage中
      const userData = {
        ...this.personalInfo,
        ...this.accountSecurity
      };
      try {
        const updatedUser = await updateUser(userId, userData);
        console.log('更新账号安全设置:', updatedUser);
        alert('账号安全设置已更新');
      } catch (error) {
        console.error('Error updating account security:', error);
        alert('更新账号安全设置失败，请重试');
      }
    },
      goBack() {
        this.$router.go(-1);
      }
    }
  }
  </script>
  
  <style scoped>
  .settings {
    max-width: 800px; /* 修改表单容器的最大宽度 */
    margin: 0 auto;
    padding: 40px;
    background-color: #f9f9f9;
    border-radius: 40px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .settings h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
  }
  
  .back-button {
    position: fixed; /* 固定位置 */
    top: 20px; /* 距离顶部 */
    left: 20px; /* 距离左边缘 */
    display: flex;
    flex-direction: column; /* 垂直排列 */
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    background: none; /* 移除背景颜色 */
    color: black; /* 文字颜色 */
    border: none; /* 移除边框 */
    cursor: pointer;
  }
  
  .back-button:hover {
    background-color: rgba(0, 0, 0, 0.1); /* 添加悬停效果 */
  }
  
  .button-icon {
    width: 24px;
    height: 24px;
    margin-bottom: 5px; /* 图标和文字之间的间距 */
  }
  
  .settings-section {
    margin-bottom: 30px;
  }
  
  .settings-section h3 {
    margin-bottom: 10px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
  }
  
  .form-group input,
  .form-group select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #2980b9;
  }
  </style>